import React, { useState, useEffect } from 'react';
import { useTheme } from '../context/ThemeContext'; //   
import './SettingsPage.css';

const SettingsPage = () => {
  const { theme, setThemeMode } = useTheme(); //  
  
  const [settings, setSettings] = useState({
    autoSave: true,
    showDeadlines: true,
    defaultStatus: 'not-started'
  });

  //  
  useEffect(() => {
    const savedSettings = localStorage.getItem('appSettings');
    if (savedSettings) {
      const parsed = JSON.parse(savedSettings);
      setSettings({
        autoSave: parsed.autoSave !== false, //   true
        showDeadlines: parsed.showDeadlines !== false, //   true
        defaultStatus: parsed.defaultStatus || 'not-started'
      });
    }
  }, []);

  //  
  useEffect(() => {
    localStorage.setItem('appSettings', JSON.stringify(settings));
  }, [settings]);

  //  
  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setSettings(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  //   
  const handleThemeChange = (e) => {
    setThemeMode(e.target.value);
  };

  //  
  const handleResetSettings = () => {
    if (window.confirm('      ?')) {
      const defaultSettings = {
        autoSave: true,
        showDeadlines: true,
        defaultStatus: 'not-started'
      };
      setSettings(defaultSettings);
      localStorage.setItem('appSettings', JSON.stringify(defaultSettings));
      setThemeMode('light'); //   
      alert(' !');
    }
  };

  //  
  const handleExportData = () => {
    const allData = {
      settings,
      theme, //    
      technologies: JSON.parse(localStorage.getItem('technology_tracker_data') || '[]'),
      exportedAt: new Date().toISOString()
    };

    const dataStr = JSON.stringify(allData, null, 2);
    const blob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `tech-tracker-backup.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);

    alert('  !');
  };

  //  
  const handleClearData = () => {
    if (window.confirm('     ?  !')) {
      localStorage.removeItem('technology_tracker_data');
      alert(' .   .');
      window.location.reload();
    }
  };

  return (
    <div className="settings-page">
      <div className="page-header">
        <h1></h1>
        <p className="page-subtitle">  </p>
      </div>

      <div className="settings-sections">
        {/*    */}
        <div className="settings-section">
          <h2 className="section-title"> </h2>
          
          <div className="setting-item">
            <label htmlFor="theme" className="setting-label">
               
            </label>
            <select
              id="theme"
              name="theme"
              value={theme}
              onChange={handleThemeChange}
              className="setting-input"
            >
              <option value="light"> </option>
              <option value="dark"> </option>
            </select>
            <p className="setting-hint">
               : <strong>{theme === 'light' ? '' : ''}</strong>
            </p>
          </div>
        </div>

        {/*   */}
        <div className="settings-section">
          <h2 className="section-title"></h2>
          
          <div className="setting-item">
            <label htmlFor="defaultStatus" className="setting-label">
                   
            </label>
            <select
              id="defaultStatus"
              name="defaultStatus"
              value={settings.defaultStatus}
              onChange={handleInputChange}
              className="setting-input"
            >
              <option value="not-started">  </option>
              <option value="in-progress">  </option>
              <option value="completed"> </option>
            </select>
          </div>

          <div className="setting-item checkbox">
            <label className="checkbox-label">
              <input
                type="checkbox"
                name="autoSave"
                checked={settings.autoSave}
                onChange={handleInputChange}
                className="checkbox-input"
              />
              <span className="checkbox-custom"></span>
               
            </label>
            <p className="setting-hint">
                  
            </p>
          </div>

          <div className="setting-item checkbox">
            <label className="checkbox-label">
              <input
                type="checkbox"
                name="showDeadlines"
                checked={settings.showDeadlines}
                onChange={handleInputChange}
                className="checkbox-input"
              />
              <span className="checkbox-custom"></span>
                
            </label>
            <p className="setting-hint">
                 
            </p>
          </div>
        </div>

        {/*    */}
        <div className="settings-section">
          <h2 className="section-title"></h2>
          
          <button 
            onClick={handleExportData}
            className="action-button export"
          >
              
          </button>
          
          <button 
            onClick={handleResetSettings}
            className="action-button reset"
          >
             
          </button>
          
          <button 
            onClick={handleClearData}
            className="action-button clear"
          >
              
          </button>
          
          <p className="warning-text">
                  
          </p>
        </div>

        {/*  */}
        <div className="settings-section">
          <h2 className="section-title"> </h2>
          
          <div className="info">
            <div className="info-item">
              <span className="info-label">:</span>
              <span className="info-value">1.0.0</span>
            </div>
            
            <div className="info-item">
              <span className="info-label">:</span>
              <span className="info-value">React, JavaScript</span>
            </div>
            
            <div className="info-item">
              <span className="info-label">:</span>
              <span className="info-value">SPA (Single Page Application)</span>
            </div>
            
            <div className="info-item">
              <span className="info-label"> :</span>
              <span className="info-value">LocalStorage </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingsPage;