// AddTechnologyPage.jsx
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useNotification } from '../context/NotificationContext'; //  
import './AddTechnologyPage.css';

const AddTechnologyPage = ({ addTechnology }) => {
  const navigate = useNavigate();
  const { showSuccess, showError, showWarning, showInfo } = useNotification(); //  
  
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    category: '',
    difficulty: 'beginner',
    resources: [''],
    status: 'not-started',
    notes: ''
  });

  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    //     
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleResourceChange = (index, value) => {
    const newResources = [...formData.resources];
    newResources[index] = value;
    setFormData(prev => ({
      ...prev,
      resources: newResources
    }));
    
    //   
    if (errors[`resource_${index}`]) {
      setErrors(prev => ({
        ...prev,
        [`resource_${index}`]: ''
      }));
    }
  };

  const addResourceField = () => {
    setFormData(prev => ({
      ...prev,
      resources: [...prev.resources, '']
    }));
  };

  const removeResourceField = (index) => {
    if (formData.resources.length > 1) {
      const newResources = formData.resources.filter((_, i) => i !== index);
      setFormData(prev => ({
        ...prev,
        resources: newResources
      }));
    } else {
      //      -  
      setFormData(prev => ({
        ...prev,
        resources: ['']
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.title.trim()) {
      newErrors.title = ' ';
      showError(',   ');
    } else if (formData.title.length > 100) {
      newErrors.title = '   (. 100 )';
      showError('   ( 100 )');
    }
    
    if (!formData.description.trim()) {
      newErrors.description = ' ';
      showError(',   ');
    } else if (formData.description.length > 1000) {
      newErrors.description = '   (. 1000 )';
      showError('   ( 1000 )');
    }
    
    //  URL 
    formData.resources.forEach((resource, index) => {
      if (resource.trim() && !isValidUrl(resource)) {
        newErrors[`resource_${index}`] = ' URL';
        showError(` #${index + 1}:  URL`);
      }
    });
    
    return newErrors;
  };

  const isValidUrl = (string) => {
    try {
      new URL(string);
      return true;
    } catch (_) {
      return false;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (isSubmitting) {
      showWarning(', ...');
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      const formErrors = validateForm();
      
      if (Object.keys(formErrors).length > 0) {
        setErrors(formErrors);
        setIsSubmitting(false);
        return;
      }
      
      //   
      const filteredResources = formData.resources.filter(r => r.trim() !== '');
      
      const newTech = {
        ...formData,
        resources: filteredResources,
        id: Date.now().toString(),
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString()
      };
      
      await addTechnology(newTech);
      
      //      NotificationContext
      showSuccess(`  "${formData.title}"  !`);
      
      //  
      setFormData({
        title: '',
        description: '',
        category: '',
        difficulty: 'beginner',
        resources: [''],
        status: 'not-started',
        notes: ''
      });
      
      //    ,    
      setTimeout(() => {
        navigate('/technologies');
      }, 1500);
      
    } catch (error) {
      console.error('Error adding technology:', error);
      showError('     ');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    const hasChanges = 
      formData.title.trim() || 
      formData.description.trim() || 
      formData.category.trim() || 
      formData.notes.trim() ||
      formData.resources.some(r => r.trim());
    
    if (hasChanges) {
      if (window.confirm('  ?     .')) {
        navigate('/technologies');
      }
    } else {
      navigate('/technologies');
    }
  };

  const handleReset = () => {
    if (window.confirm('   ?')) {
      setFormData({
        title: '',
        description: '',
        category: '',
        difficulty: 'beginner',
        resources: [''],
        status: 'not-started',
        notes: ''
      });
      setErrors({});
      showInfo(' ');
    }
  };

  return (
    <div className="add-technology-page">
      <div className="page-header">
        <h1>  </h1>
        <p className="page-subtitle">   ,   </p>
      </div>

      <form onSubmit={handleSubmit} className="technology-form">
        <div className="form-grid">
          <div className="form-section">
            <div className="form-group">
              <label htmlFor="title" className="form-label required">
                 
              </label>
              <input
                type="text"
                id="title"
                name="title"
                value={formData.title}
                onChange={handleInputChange}
                className={`form-input ${errors.title ? 'error' : ''}`}
                placeholder=": React, TypeScript, Node.js"
                maxLength={100}
                disabled={isSubmitting}
              />
              {errors.title && (
                <div className="error-message">{errors.title}</div>
              )}
              <div className="char-count">
                {formData.title.length}/100 
              </div>
            </div>

            <div className="form-group">
              <label htmlFor="description" className="form-label required">
                
              </label>
              <textarea
                id="description"
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                className={`form-textarea ${errors.description ? 'error' : ''}`}
                placeholder=",    ,   ..."
                rows="4"
                maxLength={1000}
                disabled={isSubmitting}
              />
              {errors.description && (
                <div className="error-message">{errors.description}</div>
              )}
              <div className="char-count">
                {formData.description.length}/1000 
              </div>
            </div>

            <div className="form-group">
              <label htmlFor="category" className="form-label">
                
              </label>
              <input
                type="text"
                id="category"
                name="category"
                value={formData.category}
                onChange={handleInputChange}
                className="form-input"
                placeholder=": frontend, backend, database"
                disabled={isSubmitting}
              />
            </div>
          </div>

          <div className="form-section">
            <div className="form-group">
              <label htmlFor="difficulty" className="form-label">
                
              </label>
              <select
                id="difficulty"
                name="difficulty"
                value={formData.difficulty}
                onChange={handleInputChange}
                className="form-select"
                disabled={isSubmitting}
              >
                <option value="beginner"></option>
                <option value="intermediate"></option>
                <option value="advanced"></option>
              </select>
            </div>

            <div className="form-group">
              <label className="form-label">  </label>
              <div className="resources-list">
                {formData.resources.map((resource, index) => (
                  <div key={index} className="resource-input-group">
                    <div className="resource-input-wrapper">
                      <input
                        type="url"
                        value={resource}
                        onChange={(e) => handleResourceChange(index, e.target.value)}
                        className={`form-input ${errors[`resource_${index}`] ? 'error' : ''}`}
                        placeholder="https://example.com"
                        disabled={isSubmitting}
                      />
                      {formData.resources.length > 1 && (
                        <button
                          type="button"
                          onClick={() => removeResourceField(index)}
                          className="remove-resource"
                          aria-label=" "
                          disabled={isSubmitting}
                        >
                          
                        </button>
                      )}
                    </div>
                    {errors[`resource_${index}`] && (
                      <div className="error-message">{errors[`resource_${index}`]}</div>
                    )}
                  </div>
                ))}
                <button
                  type="button"
                  onClick={addResourceField}
                  className="add-resource"
                  disabled={isSubmitting}
                >
                  +   
                </button>
              </div>
            </div>

            <div className="form-group">
              <label htmlFor="notes" className="form-label">
                 
              </label>
              <textarea
                id="notes"
                name="notes"
                value={formData.notes}
                onChange={handleInputChange}
                className="form-textarea"
                placeholder="   ..."
                rows="3"
                disabled={isSubmitting}
              />
              <div className="char-count">
                {formData.notes.length}/500 
              </div>
            </div>
          </div>
        </div>

        <div className="form-actions">
          <div className="left-actions">
            <button
              type="button"
              onClick={handleCancel}
              className="cancel-button"
              disabled={isSubmitting}
            >
              
            </button>
            <button
              type="button"
              onClick={handleReset}
              className="reset-button"
              disabled={isSubmitting}
            >
              
            </button>
          </div>
          
          <div className="right-actions">
            <button
              type="submit"
              className="submit-button"
              disabled={isSubmitting}
            >
              {isSubmitting ? (
                <>
                  <span className="spinner"></span>
                  ...
                </>
              ) : (
                ' '
              )}
            </button>
          </div>
        </div>
      </form>

      {/*     */}
      {isSubmitting && (
        <div className="submitting-overlay">
          <div className="submitting-spinner"></div>
          <p> ...</p>
        </div>
      )}
    </div>
  );
};

export default AddTechnologyPage;